import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getCategories } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { formatDistanceToNow } from 'date-fns';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { CategoryActions } from './components/CategoryActions';
import './categories.scss';

const Categories = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getCategories((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  const handleAddCategory = (newCategory) => {    
    setData(prevCategories => [newCategory, ...prevCategories]);
  };
  
  const handleEditCategory = (editedCategory) => {    
    const edit = (arr, item) => {
      return arr.map(category => {        
        if(category.id === item.id) {
          category = item;
        }
        return category;
      });
    }
    setData(prevCategories => edit(prevCategories, editedCategory))
  }
  
  const handleDeleteCategory = (categoryID) => {
    setData(prevCategories => prevCategories.filter(category => {
      return category.id !== categoryID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Categories <small className="text-muted">iEducate</small>
            </CardHeader>
            <CardBody>
              <div className="mb-2">
                <CategoryActions 
                  showAdd
                  addCallback={handleAddCategory}
                />
              </div>

              <ReactTable
                data={data}
                loading={isLoading}
                defaultPageSize={10}
                className="-striped -highlight"
                filterable
                defaultFilterMethod={(filter, row) =>
                  !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
                columns={[
                  {
                    Header: "Basic",
                    columns: [
                      {
                        Header: "icon",
                        accessor: "icon",
                        width: 75,
                        Cell: row => <Link to={`/categories/${row.original.id}`}></Link>,
                        filterable: false
                      },
                      {
                        Header: "name",
                        accessor: "name",
                      }
                    ]
                  },
                  {
                    Header: "Info",
                    columns: [
                      {
                        Header: "actions",
                        accessor: "id",
                        filterable: false,
                        width: 150,
                        Cell: ({ original }) => (
                          <CategoryActions 
                            showDelete 
                            showEdit 
                            category={original} 
                            deleteCallback={handleDeleteCategory}
                            editCallback={handleEditCategory}
                          />
                        )
                      },
                    ]
                  }
                ]}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Categories;
